<template>
  <v-container>
    <div class="display-1 text-center mt-5"> ETriage Infirmary </div>

    <div class="text-center ma-12">
      <v-progress-circular
        v-if="formLoading"
        :size="150"
        class="mx-3"
        indeterminate
        color="primary"
      >
      </v-progress-circular>
    </div>

    <v-alert type="error" class="text-center" v-if="!this.accessRightsForClearing && !formLoading && !cardFomrLoading">
      <span class="text-h5">
        {{ formMessage }}
      </span>
    </v-alert>
    
    <div v-if="this.accessRightsForClearing">
      <v-menu
        ref="menu"
        v-model="menu"
        :close-on-content-click="false"
        :return-value.sync="date"
        transition="scale-transition"
        offset-y
        min-width="290px"
        v-if="this.accessRightsForClearing && !formLoading"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
            v-model="date"
            label="Date"
            prepend-icon="fas fa-calendar"
            readonly
            v-bind="attrs"
            v-on="on"
          ></v-text-field>
        </template>
        <v-date-picker v-model="date" no-title scrollable>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
          <v-btn text color="primary" @click="changeDate">OK</v-btn>
        </v-date-picker>
      </v-menu>
      
      <div class="d-flex flex-wrap mt-5">
        <v-row>
          <v-col v-if="!formLoading" >
            <Card :header="'Employees For Covid ER Report'" class="error" :employees="eTriageEREmployees" :date="date" :userType="'employees'" />
          </v-col>
          <!-- <v-col  v-if="!cardFormLoading">
            <Card :header="'All ETriage for Covid ER'" class="error" :employees="allETriageEREmployees" />
          </v-col> -->
          <v-col v-if="!formLoading" >
            <Card :header="'Visitors For Covid ER Report'" class="error" :employees="eTriageERVisitors" :date="date" :userType="'visitors'" />
          </v-col>

          <v-col v-if="!formLoading" >
            <Card :header="'TPCs For Covid ER Report'" class="error" :employees="eTriageERTPC" :date="date" :userType="'tpc'" />
          </v-col>
        </v-row>
      </div>
    </div>
  </v-container>
</template>

<script>
import Card from "../components/core/Card";
import { mapGetters } from 'vuex';
export default {
  components: {
    Card
  },
  data() {
    return {
      date: new Date().toISOString().substr(0, 10),
      menu: false,
    }
  },
  mounted() {
    if (!localStorage.isLoggedIn) {
      this.$router.push("/");
    }
  },
  computed: {
    ...mapGetters([
      "accessRightsForClearing",
      "formLoading",
      "cardFormLoading",
      "allETriageEREmployees",
      "eTriageEREmployees",
      "eTriageERVisitors",
      "eTriageERTPC",
      "userDetails",
      "accessRightsForClearing",
      "formMessage",
    ])
  },
  created() {
    this.checkAccessRightsForClearing();
    this.getETriageToday();
    this.getAllTriageEREmployees();
    this.getETriageOtherUsersToday();
  },
  methods: {
    async changeDate(){
      this.$refs.menu.save(this.date)
      await this.getETriageToday();
      await this.getETriageOtherUsersToday();
    },
    async checkAccessRightsForClearing() {
      await this.$store.dispatch("checkAccessRightsForClearing", this.userDetails.userCode);
    },
    async getAllTriageEREmployees() {
      await this.$store.dispatch("getAllTriageEREmployees");
    },
    async getETriageToday() {
      await this.$store.dispatch("getETriageEmployees", this.date);
    },
    async getETriageOtherUsersToday() {
      await this.$store.dispatch("getETriageOtherUsers",this.date);
    },
  },
};
</script>

